import React, { useEffect, useRef } from 'react';

const RainBackground = () => {
  const canvasRef = useRef(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;
    const ctx = canvas.getContext('2d');
    let animationFrameId;

    const resize = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
    };

    window.addEventListener('resize', resize);
    resize();

    const raindrops = [];
    const dropCount = 25; // Extremely sparse for elegance
    const colors = ['#ffffff', '#60a5fa'];

    class Drop {
      constructor() {
        this.init();
      }

      init() {
        this.x = Math.random() * canvas.width;
        this.y = Math.random() * -canvas.height;
        this.speed = 0.2 + Math.random() * 0.8; // Glacial movement
        this.length = 15 + Math.random() * 25;
        this.width = 0.3; // Hairline thin
        this.color = colors[Math.floor(Math.random() * colors.length)];
        this.opacity = 0.02 + Math.random() * 0.04; // Barely visible
      }

      update() {
        this.y += this.speed;
        if (this.y > canvas.height) {
          this.init();
        }
      }

      draw() {
        ctx.strokeStyle = this.color;
        ctx.lineWidth = this.width;
        ctx.globalAlpha = this.opacity;
        ctx.beginPath();
        ctx.moveTo(this.x, this.y);
        ctx.lineTo(this.x, this.y + this.length);
        ctx.stroke();
      }
    }

    for (let i = 0; i < dropCount; i++) {
      raindrops.push(new Drop());
    }

    const render = () => {
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      
      raindrops.forEach(drop => {
        drop.update();
        drop.draw();
      });

      animationFrameId = requestAnimationFrame(render);
    };

    render();

    return () => {
      window.removeEventListener('resize', resize);
      cancelAnimationFrame(animationFrameId);
    };
  }, []);

  return (
    <canvas
      ref={canvasRef}
      className="fixed inset-0 pointer-events-none z-[-10]"
      style={{ background: 'transparent' }}
    />
  );
};

export default RainBackground;
